export default function getLabelText(label, lang) {
    if (lang == 'en') {
        return label.label_en;
    } else if (lang == 'fr') {
        return label.label_fr;
    } else if (lang == 'sp') {
        return label.label_sp;
    } else if (lang == 'pr') {
        return label.label_pr;
    } else {
        return label.label_en;
    }
}